.top-nav {
    background-color: #101010;
    color: $navgreyclr;
    padding: 16px 32px;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0;
    .back-arrow,.fwd-arrow,.signup-btn,.login-btn {
        fill: $navgreyclr;
        color: $navgreyclr;
        background-color: unset;
        border: unset;
    }
    .arrows-btns {
        height: rem(32px);
        width: rem(80px);
        align-items: center;
        justify-content: space-between;
        .back-arrow,.fwd-arrow {
            height: rem(32px);
            padding-left: rem(5px);
            padding-right: rem(5px);
            & svg {
                width: rem(22px);
                height: rem(22px);
            }
        }
    }
    .authorization-btns {
        align-items: center;
        .signup-btn {
            font-weight: 700;
            font-size: 1rem;
            color:#6a6a6a;
            padding: 8px 32px 8px 8px;
            height: rem(48px);
            width: rem(99px);
            font-family: CircularSp,CircularSp-Arab,CircularSp-Hebr,CircularSp-Cyrl,CircularSp-Grek,CircularSp-Deva,sans-serif;
        }
        .login-btn {
            font-weight: 700;
            font-size: 1rem;
            border: 0px;
    border-radius: 500px;
    background-color: #ffffff;
    color:#000;
    width: rem(109.375px);
    height: rem(48px);
        }

    }
}